import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
// temp
import { login } from "../actions/clientAction";

class LoginForm extends Component {
  state = {
    mail: "",
    password: "",
    isLogIn: null,
  };
  changeInput = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };
  formSubmit = (e) => {
    e.preventDefault();
    const client = {
      mail: this.state.mail,
      password: this.state.password,
    };
    this.props.login(client.mail, client.password);
    this.setState({ mail: "", password: "" });
    // костыль
    setTimeout(() => {
      console.log(this.props.isLogIn);
      this.setState({
        isLogIn: this.props.isLogIn,
      });
    }, 1000);
  };
  render() {
    if (this.state.isLogIn) {
      return <Redirect to="/newOrder" />;
    }
    return (
      <div>
        <div className="form_wrap">
          <h2>Log In</h2>
          <form onSubmit={this.formSubmit}>
            <label>
              <span>Введите email:</span>
              <input
                type="email"
                name="mail"
                placeholder="email"
                value={this.state.mail}
                onChange={this.changeInput}
              />
            </label>
            <label>
              <span>Введите пароль:</span>
              <input
                type="password"
                name="password"
                placeholder="пароль"
                value={this.state.password}
                onChange={this.changeInput}
              />
            </label>
            <div className="btn_wrap">
              <button type="submit">Log In</button>

              <Link to="/">
                <button>Отмена</button>
              </Link>
            </div>
            <div className="btn_wrap">
              <span>Еще нет аккаунта?</span>
              <Link to="/signin">Зарегистрируйтесь</Link>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  isLogIn: state.clients.isAuth,
  data: state.clients.client,
});
export default connect(mapStateToProps, { login })(LoginForm);
