import {
  GET_ALL_CLIENTS,
  LOGIN,
  LOGIN_ERROR,
  POST_NEW_CLIENT,
  UPDATE_CLIENT,
  DELETE_CLIENT,
} from "../actions/types";

const initialState = {
  clients: [],
  client: {},
};
export default (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_CLIENTS:
      return {
        ...state,
        clients: action.payload,
      };
    case LOGIN:
      return {
        ...state,
        isAuth: true,
        client: action.payload,
      };
    case LOGIN_ERROR:
      return {
        ...state,
        isAuth: false,
        client: action.payload,
      };
    case POST_NEW_CLIENT:
      return {
        ...state,
        client: action.payload,
      };
    default:
      return state;
  }
};
